<template>
  <!-- 电影详情 -->
  <div class="details-move" :id="this.detamoves.id" @scroll="getScroll()">
    <div class="details-header">
      <van-nav-bar title="电影详情" left-arrow :border="false" @click-left="back">
        <van-icon name="share" size="20px" slot="right" color="white" />
        <van-icon name="arrow-left" size="19px" slot="left" color="white" />
      </van-nav-bar>
    </div>
    <div class="deta-return"></div>
    <div class="class-moves">
      <!-- 电影基本信息 -->
      <div class="moves-message">
        <!-- 电影信息 -->
        <div class="moves-fonte">
          <div class="fonte-heard">
            <div class="heard-img">
              <img :src="this.detamoves.images.small" />
            </div>
            <div class="heard-fr-text">
              <p class="text-fo1">{{this.detamoves.title}}</p>
              <p class="text-fo2">{{this.detamoves.original_title}}</p>
              <div class="text-fo3">
                <p>类型:{{this.detamoves.genresInfo}}</p>
                <p>影片时长:{{this.detamoves.hour}}小时{{this.detamoves.minute}}分钟</p>
                <p>{{this.detamoves.pubdate}}在中国上映</p>
              </div>
            </div>
          </div>
          <div class="fonte-bottom">
            <!-- 评分详情 -->
            <div class="evaluate-lt">
              <div class="evaluate-title">
                <span>淘票票评分</span>
                <span>
                  查看评价
                  <van-icon name="arrow" size="10" color="#c3c6cc" />
                </span>
              </div>
              <div class="evaluate-text">
                <div class="evaluate-fl">
                  <span>{{this.detamoves.rating.average}}</span>
                  <div class="grade-tol">
                    <div class="rangking-grade">
                      <el-rate
                        v-model="this.detamoves.evaluate"
                        disabled
                        disabled-void-color="#CFD0D1"
                        score-template="{value}"
                      ></el-rate>
                    </div>
                    <div class="pingfen">{{this.detamoves.ratings_count}}条评分</div>
                  </div>
                  <!-- -- -->
                </div>
                <div class="evaluate-fr">
                  <div class="grade-plan">
                    <span>9-10分</span>
                    <van-progress :percentage="12" :show-pivot="false" />
                    <span>82%</span>
                  </div>
                  <div class="grade-plan">
                    <span>5-8分</span>
                    <van-progress :percentage="72" :show-pivot="false" />
                    <span>82%</span>
                  </div>
                  <div class="grade-plan">
                    <span>1-4分</span>
                    <van-progress :percentage="8" :show-pivot="false" />
                    <span>82%</span>
                  </div>
                </div>
              </div>
            </div>
            <!-- -- -->
            <!-- 自己评价 -->
            <div class="evaluate-rt">
              <div class="evaluate-logo">
                <van-icon
                  :name="iconn"
                  :color="iconcolor"
                  size="20"
                  @click="join(iconn,show)"
                  :v-model="show"
                />
                <span>想看</span>
              </div>
              <div class="evaluate-logo">
                <van-icon name="star-o" size="20" />
                <span>看过</span>
              </div>
              <div class="evaluate-logo">
                <van-icon name="edit" size="20" />
                <span>点评</span>
              </div>
            </div>
            <!-- end -->
          </div>
        </div>
        <!-- end -->
      </div>
      <!-- end -->
      <!-- 剧情简介 -->
      <div class="scenario">
        <div class="scenario-title">剧情简介</div>
        <div class="scenario-text">
          <div :style="{display}" class="scenario-text1">{{this.detamoves.summary}}</div>
          <span @click="changeDisplay(display)" class="unfold">{{this.name}}</span>
        </div>
      </div>
      <!-- end -->
      <!-- 演员人员 -->
      <div class="actor-star">
        <div class="about-head">
          <div class="sp1">
            <span>演职人员</span>
          </div>
          <div class="sp2">
            <span>{{this.detamoves.starindex1}}位演员</span>
            <van-icon name="arrow" size="8" color="#DFE1E6" />
          </div>
        </div>
        <div class="star-uls">
          <ul :style="{width: this.maxsaw1+this.maxsaw2 + 'px'}">
            <li ref="listw1" v-for="(item, index) in this.detamoves.writers" :key="'1'+index">
              <div class="star-img">
                <img :src="item.avatars.small" />
              </div>
              <div class="star-name">
                <p>{{item.name}}</p>
                <p>导演</p>
              </div>
            </li>
            <li ref="listw1" v-for="(item, index) in this.detamoves.casts" :key="'2'+index">
              <div class="star-img">
                <img :src="item.avatars.small" />
              </div>
              <div class="star-name">
                <p>{{item.name}}</p>
                <p>演员</p>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <!-- end -->
      <!-- 预告片剧照 -->
      <div class="prevue">
        <div class="about-head">
          <div class="sp1">
            <span>预告片/剧照</span>
          </div>
          <div class="sp2">
            <span>{{this.detamoves.starindex2}}张剧照</span>
            <van-icon name="arrow" size="8" color="#DFE1E6" />
          </div>
          <div class="stage-moves">
            <ul :style="{width: this.maxsaw3+this.maxsaw4 + 'px'}">
              <!-- 预告片 -->
              <li
                @click="getvideo({name: 'videot', vocat: {id: item.id}})"
                class="detamoves"
                ref="listw3"
                v-for="(item, index) in this.detamoves.trailers"
                :key="'1'+index"
                :id="item.id"
              >
                <img :src="item.small" />
                <van-icon name="play-circle-o" color="white" size="40" />
              </li>
              <!-- 剧照 -->
              <li
                ref="listw3"
                v-for="(item, index) in this.detamoves.photos"
                :key="'2'+index"
                :id="item.id"
              >
                <img :src="item.cover" />
              </li>
            </ul>
          </div>
        </div>
      </div>
      <!-- end -->
      <!-- 观众影评 -->
      <div class="fir-review">
        <div class="about-head">
          <div class="sp1">
            <span>观众影评</span>
          </div>
          <div class="sp2">
            <span>{{this.detamoves.starindex3}}条评论</span>
            <van-icon name="arrow" size="8" color="#DFE1E6" />
          </div>
        </div>
        <div class="review">
          <!-- 条目 -->
          <div class="review-li">
            <van-skeleton title avatar :row="3" :loading="loading">
              <div
                class="review-list"
                v-for="(item, index) in this.detamoves.popular_reviews"
                :key="index"
                :id="item.id"
              >
                <div class="review-img">
                  <img :src="item.author.avatar" />
                </div>
                <div class="review-text">
                  <h3>{{item.author.name}}</h3>
                  <div class="review-lis">
                    <el-rate
                      v-model="item.rating.value"
                      disabled
                      show-score
                      text-color="#ff9900"
                      disabled-void-color="#CFD0D1"
                      score-template="{value}"
                    ></el-rate>
                  </div>
                  <div class="review-txt">{{item.summary}}</div>
                </div>
                <div class="review-timer">
                  <div class="timer-fl">
                    <span>02-05</span>
                    <span>12:05</span>
                  </div>
                  <div class="timer-fr">
                    <div class="logo1">
                      <van-icon name="good-job-o" size="20" />
                      <span>999</span>
                    </div>
                    <div class="logo1">
                      <van-icon name="chat-o" size="20" />
                      <span>999</span>
                    </div>
                  </div>
                </div>
              </div>
            </van-skeleton>
          </div>
          <!-- end -->
        </div>
      </div>
      <!-- end -->
      <!-- 大V评论 -->
      <div class="V-comment">
        <div class="about-head">
          <div class="sp1">
            <span>大V评论</span>
          </div>
          <div class="sp2">
            <span>{{this.detamoves.starindex4}}条大V评论</span>
            <van-icon name="arrow" size="8" color="#DFE1E6" />
          </div>
        </div>
        <div class="V-carousel">
          <ul :style="{width:this.maxsaw5+'px'}">
            <li
              ref="listw4"
              v-for="(item,index) in this.detamoves.popular_comments"
              :key="index"
              :id="item.id"
            >
              <div class="carousel-portrait">
                <div class="protrait-fl">
                  <div class="portrait">
                    <img :src="item.author.avatar" />
                  </div>
                  <div class="portrait-text">
                    <h2>{{item.author.name}}</h2>
                    <p>大V</p>
                  </div>
                </div>
                <div class="protrait-fr">
                  <span>{{item.rating.value}}.0</span>
                </div>
              </div>
              <div class="carousel-text">{{item.content}}</div>
            </li>
          </ul>
        </div>
      </div>
      <!-- end -->
      <div class="choose-eat">
        <div class="eat-btn">特惠选座</div>
      </div>
    </div>
  </div>
</template>
<style lang="less">
.class-moves {
}
.details-header {
  position: sticky;
  left: 0;
  top: 0;
  width: 100%;
  background: #8d2712;
  z-index: 33;
  .van-nav-bar {
    background: #8d2712;
    color: white;
    .van-nav-bar__title {
      color: white;
    }
  }
}
.details-move {
  background: #f5f6fa;
  position: relative;

  .deta-return {
    background: #8d2712;
    height: 134px;
  }
  .moves-message {
    width: 100%;
    height: 280px;
    background: white;
    position: relative;
    margin-top: -11px;
    border-top-left-radius: 16px;
    border-top-right-radius: 16px;
    .moves-fonte {
      width: 345px;

      position: absolute;

      left: 14px;
      top: -60px;

      border-bottom-left-radius: 7px;
      border-bottom-right-radius: 7px;
      .fonte-heard {
        float: left;
        width: 100%;
        height: 140px;
        .heard-img {
          float: left;
          width: 100px;
          height: 100%;
          border-radius: 8px;
          overflow: hidden;
          img {
            width: 100px;
            height: 100%;
          }
        }
        .heard-fr-text {
          float: left;
          padding-left: 15px;
          overflow: hidden;
          p {
            max-width: 200px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          .text-fo1 {
            margin: 5px 0;
            font-size: 18px;
            color: white;
          }
          .text-fo2 {
            line-height: 22px;
            margin: 0;
            color: #ddc6bd;
          }
          .text-fo3 {
            overflow: hidden;
            margin-top: 14px;
            p {
              margin: 0;
            }
            p:nth-child(1) {
              margin-bottom: 5px;
            }
            p:nth-child(2) {
              margin-bottom: 12px;
            }
            p:nth-child(3) {
              color: #d2d3d8;
              font-size: 12px;
            }
          }
        }
      }
      // --
      .fonte-bottom {
        width: 325px;

        padding: 10px;
        float: left;
        margin-top: 17px;
        background: white;
        border-top: 0.5px solid #dfe1e6;
        box-shadow: 0px 2px 4px 0px #dfe1e6;
        .evaluate-lt {
          float: left;
          width: 100%;
          .evaluate-title {
            width: 100%;
            height: 32px;
            font-size: 14px;
            line-height: 32px;
            span:nth-child(1) {
              float: left;
              color: #5b5f66;
              font-weight: 600;
            }
            span:nth-child(2) {
              float: right;
              font-size: 10px;
              color: #c3c6cc;
            }
          }
          .evaluate-text {
            float: left;
            width: 100%;
            margin-top: 10px;
            .evaluate-fl {
              overflow: hidden;

              height: 45px;
              float: left;
              border-right: 2px solid #f8f8fa;
              span {
                font-size: 29px;
                color: #2a97f5;
                font-weight: 600;
                margin-left: 5px;
                float: left;
              }
              .pingfen {
                float: left;
                font-size: 12px;
                color: #999;
                margin-left: 10px;
              }
              .grade-tol {
                float: left;
                .rangking-grade {
                  width: 100px;
                  height: 20px;
                  position: relative;
                  span {
                    position: absolute;
                    width: 18px;
                    height: 18px;
                    top: 0;
                    font-size: 16px;

                    &:nth-child(1) {
                      left: 0;
                    }
                    &:nth-child(2) {
                      left: 18px;
                    }
                    &:nth-child(3) {
                      left: 36px;
                    }
                    &:nth-child(4) {
                      left: 54px;
                    }
                    &:nth-child(5) {
                      left: 72px;
                    }
                  }
                }
              }
            }
            .evaluate-fr {
              float: left;
              margin-left: 15px;
              font-size: 12px;
              color: #b1b4bc;
              div {
                height: 15px;
                line-height: 15px;
              }
              span:nth-child(1) {
                width: 50px;
                float: left;
              }
              span:nth-child(2) {
                float: right;
              }
              .van-progress {
                width: 62px;
                height: 4px;
                float: left;
                margin-top: 5px;
                margin-left: 10px;
                margin-right: 10px;
              }
            }
          }
        }
        .evaluate-rt {
          width: 100%;
          height: 40px;
          line-height: 40px;
          margin-top: 10px;
          float: left;
          border-top: 1px solid #f1f2f5;
          font-size: 14px;
          .evaluate-logo:nth-child(1) {
            margin-left: 12px;
          }
          .evaluate-logo {
            float: left;
            margin-left: 60px;
            width: 60px;
            height: 100%;
            position: relative;
            i {
              position: absolute;
              top: 10px;
            }
            span {
              position: absolute;
              left: 30px;
            }
          }
        }
      }
    }
  }
  //    --
  .scenario {
    width: calc(~"100%-28px");
    overflow: hidden;
    padding: 14px;
    margin-top: 15px;
    background: white;
    .scenario-title {
      font-size: 14px;
      font-weight: 600;
      line-height: 40px;
    }
    .scenario-text {
      width: 100%;
      position: relative;
      font-size: 13px;
      .scenario-text1 {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
        line-height: 25px;
        margin-bottom: 10px;
      }
      .unfold {
        display: inline-block;
        color: #2a97f5;
        position: absolute;
        right: 8px;
        bottom: -10px;

        right: -10px;
      }
    }
  }

  // --
  .actor-star {
    margin-top: 10px;
    width: 100%;
    background: white;
    .about-head {
      height: 50px;
      line-height: 50px;
      font-size: 17px;
      background: white;
      .sp1 {
        float: left;
        margin-left: 14px;
        span {
          font-size: 16px;
          font-weight: 600;
          color: black;
        }
      }
      .sp2 {
        float: right;
        margin-right: 12px;
        span {
          font-size: 12px;
          color: #dfe1e6;
        }
      }
    }
    .star-uls {
      width: 100%;
      height: 121px;
      padding-top: 6px;
      overflow-x: auto;
      overflow-y: hidden;
      ul {
        width: 1200px;
        padding-left: 8px;
        height: 100%;
        li {
          float: left;
          width: 70px;
          height: 100%;
          margin-left: 7px;
          .star-img {
            width: 100%;
            height: 70px;
            border-radius: 7px;
            overflow: hidden;
            img {
              width: 100%;
              height: 70px;
            }
          }
          .star-name {
            margin-top: 8px;
            p {
              margin: 0;
            }
            p:nth-child(1) {
              font-size: 14px;
              color: black;
              max-width: 70px;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
            p:nth-child(2) {
              font-size: 12px;
              color: #999;
              margin-top: 5px;
            }
          }
        }
      }
    }
  }
  // --
  .prevue {
    width: 100%;
    margin-top: 15px;
    background: white;
    height: 170px;
    .about-head {
      height: 50px;
      line-height: 50px;
      font-size: 17px;
      background: white;
      .sp1 {
        float: left;
        margin-left: 14px;
        span {
          font-size: 16px;
          font-weight: 600;
          color: black;
        }
      }
      .sp2 {
        float: right;
        margin-right: 12px;
        span {
          font-size: 12px;
          color: #dfe1e6;
        }
      }
    }
    .stage-moves {
      width: 100%;
      padding-top: 6px;
      overflow-x: auto;
      padding-bottom: 13px;
      ul {
        width: 3000px;
        padding-left: 8px;

        .detamoves {
          position: relative;
          i {
            z-index: 20;
            position: absolute;
            left: 40px;
            top: 30px;
          }
        }
        li {
          float: left;
          width: 132px;
          height: 95px;
          margin-left: 9px;
          border-radius: 7px;
          overflow: hidden;
          img {
            width: 132px;
            height: 95px;
          }
        }
      }
    }
  }
  // --
  .fir-review {
    width: 100%;
    margin-top: 15px;
    background: white;
    .about-head {
      height: 50px;
      line-height: 50px;
      font-size: 17px;
      background: white;
      .sp1 {
        float: left;
        margin-left: 14px;
        span {
          font-size: 16px;
          font-weight: 600;
          color: black;
        }
      }
      .sp2 {
        float: right;
        margin-right: 12px;
        span {
          font-size: 12px;
          color: #dfe1e6;
        }
      }
    }
    .review {
      overflow: hidden;
      width: 100%;
      .review-li {
        width: 100%;
        float: left;
        padding-bottom: 20px;
        border-bottom: 1px solid #f6f6f7;
        .review-list {
          padding: 0 16px;
          overflow: hidden;
          .review-img {
            border-radius: 100%;
            width: 38px;
            height: 38px;
            margin-right: 16px;
            margin-top: 15px;
            float: left;

            img {
              width: 38px;
              height: 38px;
              border-radius: 100%;
            }
          }
          .review-text {
            float: left;
            padding-top: 8px;
            width: 275px;
            h3 {
              max-width: 50%;
              margin: 0;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
            .review-lis {
              max-width: 100%;
              margin-top: 10px;
              margin-bottom: 10px;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
            .review-txt {
              width: 100%;
              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-line-clamp: 3;
              line-height: 20px;
              font-size: 12px;
              -webkit-box-orient: vertical;
            }
          }
          .review-timer {
            width: 100%;
            height: 30px;
            line-height: 30px;
            float: left;
            padding: 10px 0;
            font-size: 13px;
            color: #979ca7;
            .timer-fl {
              float: left;
              overflow: hidden;
              height: 100%;
              span:nth-child(2) {
                margin-left: 15px;
              }
            }
            .timer-fr {
              float: right;
              height: 100%;
              .logo1:nth-child(2) {
                margin-left: 15px;
              }
              .logo1 {
                float: left;
                width: 52px;
                height: 100%;
                position: relative;
                i {
                  position: absolute;
                  top: 5px;
                }
                span {
                  position: absolute;
                  left: 25px;
                }
              }
            }
          }
        }
      }
    }
  }
  // --
  .V-comment {
    width: 100%;
    overflow: hidden;
    margin-top: 15px;
    background: white;
    .about-head {
      height: 50px;
      line-height: 50px;
      font-size: 17px;
      background: white;
      .sp1 {
        float: left;
        margin-left: 14px;
        span {
          font-size: 16px;
          font-weight: 600;
          color: black;
        }
      }
      .sp2 {
        float: right;
        margin-right: 12px;
        span {
          font-size: 12px;
          color: #dfe1e6;
        }
      }
    }
    .V-carousel {
      width: 100%;
      height: 190px;
      overflow-x: auto;
      overflow-y: hidden;
      padding: 8px 0 16px 0;
      ul {
        height: 100%;
        li {
          float: left;
          width: 284px;
          height: 180px;
          overflow: hidden;
          padding: 12px;
          margin-left: 15px;
          border-radius: 7px;
          box-shadow: 0px 2px 6px 0px #dfe1e6;
          border-bottom: 0.5px solid #dfe1e6;
          .carousel-portrait {
            width: 100%;
            float: left;
            .protrait-fl {
              overflow: hidden;
              float: left;
              .portrait {
                width: 40px;
                height: 40px;
                border-radius: 100%;
                background: red;
                overflow: hidden;
                float: left;
                img {
                  width: 40px;
                  height: 40px;
                  border-radius: 50%;
                }
              }
              .portrait-text {
                float: left;
                overflow: hidden;
                margin-left: 10px;
                h2 {
                  margin: 0;
                  font-size: 14px;
                }
                p {
                  margin: 0;
                  color: #979ca7;
                }
              }
            }
            .protrait-fr {
              float: right;

              span {
                font-size: 20px;
                font-weight: 600;
                line-height: 40px;
                color: #2a97f5;
              }
            }
          }
          .carousel-text {
            float: left;
            margin-top: 15px;
            width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 4;
            line-height: 25px;
            font-size: 15px;
            -webkit-box-orient: vertical;
          }
        }
      }
    }
  }
  // --
  .choose-eat {
    width: 100%;
    height: 56px;
    margin-top: 10px;
    padding-top: 10px;
    background: white;
    .eat-btn {
      width: 290px;
      height: 42px;
      background: #e81e1c;
      line-height: 42px;
      text-align: center;
      margin: 0 auto;
      font-size: 16px;
      color: white;
      font-weight: 600;
      border-radius: 5px;
      box-shadow: 0px 2px 5px 0px #dfe1e6;
    }
  }
}
</style>
<script>
export default {
  name: "detailsv",
  data() {
    return {
      display: "-webkit-box",
      iconn: "like-o",
      iconcolor: "black",
      name: "展开",
      loading: false,
      show: false,
      detamoves: "",
      starindex1: 0,
      starindex2: 0,
      starindex3: 0,
      starindex4: 0,
      maxsaw1: 0,
      maxsaw2: 0,
      maxsaw3: 0,
      maxsaw4: 0,
      maxsaw5: 0,
      changValue: null
    };
  },
  methods: {
    // 返回上一步
    back() {
      this.$router.go(-1);
    },
    getvideo(o) {
      this.$router.push(o);
      localStorage.setItem("videoID", o.vocat.id);
    },
    // 展开
    changeDisplay(display) {
      this.display = display == "block" ? "-webkit-box" : "block";
      return (this.name = display == "block" ? "展开" : "收起");
    },

    Detamove() {
      //开启加载提示
      this.$toast.loading({
        duration: 0,
        message: "加载中..."
      });

      //截取路由参数
      let id = this.$route.params.id;

      localStorage.setItem("MovesID", id);
      //获取电影详情
      this.axios({
        method: "GET",
        url: "https://douban.uieee.com/v2/movie/subject/" + id
      })
        .then(result => {
          //将电影类型合并
          result.data.genresInfo = result.data.genres.join(" 、");
          //时间转换
          result.data.hour = parseInt(
            result.data.durations[0].slice(0, -2) / 60
          );
          result.data.minute = result.data.durations[0].slice(0, -2) % 60;
          //评分
          result.data.evaluate = result.data.rating.average / 2;
          //获取演职员的人数
          result.data.starindex1 = 0;
          result.data.casts.forEach(v => {
            result.data.starindex1 += 1;
          });
          //获取剧照数量
          result.data.starindex2 = 0;
          result.data.photos.forEach(v => {
            result.data.starindex2 += 1;
          });
          //获取评论
          result.data.starindex4 = 0;
          result.data.popular_reviews.forEach(v => {
            result.data.starindex3 += 1;
          });
          //获取大V评论
          result.data.starindex4 = 0;
          result.data.popular_comments.forEach(v => {
            result.data.starindex4 += 1;
          });
          //设置ul宽度
          //等页面渲染完成再获取
          setTimeout(() => {
            this.changValue = 1;
            this.$nextTick(() => {
              //动态设置演员部分ul的宽度
              let listW1 = this.$refs.listw1[0].clientWidth + 20;
              this.maxsaw1 = listW1 * result.data.writers.length;
              this.maxsaw2 = listW1 * result.data.casts.length;
              //设置剧照部分
              let listW2 = this.$refs.listw3[0].clientWidth + 10;
              this.maxsaw3 = listW2 * result.data.trailers.length;
              this.maxsaw4 = listW2 * result.data.photos.length;
              //设置大V部分
              let listW3 = this.$refs.listw4[0].clientWidth + 20;
              this.maxsaw5 = listW3 * result.data.popular_comments.length;
            });
          }, 100);

          localStorage.setItem("detamove", JSON.stringify(result.data));
          this.detamoves = result.data;
          this.$toast.clear();
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    // 收藏
    join(iconn, show) {
      //收藏图标状态
      this.iconn = iconn == "like" ? "like-o" : "like";
      this.iconcolor = iconn == "like" ? "black" : "red";
      this.show = show == false ? true : false;
      //获取用户登录状态
      let user = localStorage.getItem("user");
      //用户是否存在
      if (user) {
        user = JSON.parse(user);
        //  不存在则提示登录
        if (!user.isLogin) {
          this.$toast({ message: "请先去登录" });
          return this.$router.push({ name: "register" });
        }
        //获取已喜欢的电影
        let likemoves = localStorage.getItem("likemoves");
        likemoves = likemoves ? JSON.parse(likemoves) : {};
        let currentLimove = likemoves[user.username];

        if (!currentLimove) {
          likemoves[user.username] = [];
        }
        this.detamoves.show = this.show;
        if (this.show) {
          for (let i = 0; i < likemoves[user.username].length; i++) {
            if (likemoves[user.username][i].id == this.detamoves.id) {
              //如果喜欢的电影相同,则阻断
              return;
            }
          }
          likemoves[user.username].unshift(this.detamoves);
          localStorage.setItem("likemoves", JSON.stringify(likemoves));
          this.$toast({ message: "已加入收藏" });
        } else {
          for (let i = 0; i < likemoves[user.username].length; i++) {
            if (likemoves[user.username][i].id == this.detamoves.id) {
              likemoves[user.username].splice(i, 1);
            }
          }
          localStorage.setItem("likemoves", JSON.stringify(likemoves));
          this.$toast({ message: "已取消加入收藏" });
        }
      } else {
        this.$toast({ message: "请登录" });
        this.$router.push({ name: "register" });
      }
    }
  },
  created() {
    this.Detamove();
    //获取用户登录状态
    let user = JSON.parse(localStorage.getItem("user"));
    let detamoves = JSON.parse(localStorage.getItem("detamove"));
    //获取收藏的电影
    let likemoves = localStorage.getItem("likemoves");
    likemoves = likemoves ? JSON.parse(likemoves) : {};
    let currentLimove = likemoves[user.username];
    if (likemoves) {
      for (let i = 0; i < currentLimove.length; i++) {
        //如果电影存在
        if (currentLimove[i].id == detamoves.id) {
          this.detamoves = currentLimove[i];
          this.show = true;
          this.iconn = "like";
          this.iconcolor = "red";
          return
        }else{
          this.show = false;
        this.iconn="like-o";
      this.iconcolor="black";
        }
        
      }
    }
  }
};
</script>